<template>
  <div class="home-container">
    <van-nav-bar
  title="头条"
  :fixed="true"
  class="yangshi"
/>
<van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="ok">
<van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  @load="onLoad"
>
  <van-cell v-for="item in list" :key="item" :title="item" />
<Article v-for="item in Articlelist" :key="item.id" 
:title="item.title"
:aut_name="item.aut_name"
:comm_count="item.comm_count"
:pubdate="item.pubdate"
:cover="item.cover"
></Article>
</van-list>
</van-pull-refresh>
  </div>

</template>

<script>

import { getarticles } from '../../api/getinfo.js'
import Article from '../../components/articlelist/article.vue'
export default {
data(){

    return {
      page: 1,
      limit: 10,
      Articlelist: [],
      loading: true,
      finished: false,
      isLoading: false,
      ok:false
}

  },
methods:{
  async initdate() {

    const { data: res } = await getarticles(this.page,this.limit)
    // this.Articlelist = res;
    this.Articlelist=[...this.Articlelist,...res]
    this.loading = false;
    if (res.length == 0) {

      this.finished = true;
      console.log('d');
}



  },
  async initdate2() {

const { data: res } = await getarticles(this.page,this.limit)
// this.Articlelist = res;
this.Articlelist=[...res,...this.Articlelist]
this.isLoading = false;
    if (this.page === 7) {
  this.ok=true
}



},
onLoad(){

  this.page++;
  this.initdate();


  },
onRefresh(){

  this.page++;
  this.initdate2();

}


  },
  created() {

    this.initdate2();
    
    
  },
components:{

  Article,

}
}
</script>

